import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { MatSnackBar, MatSnackBarConfig } from '@angular/material';

@Injectable({
  providedIn: 'root'
})
export class MassageMasterService {

  baseUrl = '/assets/vahanaa/data/';
  endpoint = {
    auth: `${this.baseUrl}auth.json`,
  };
  messageJson = {};
  defaultsError = {};

  constructor(private http: HttpClient, private snackBar: MatSnackBar) { }

  public getErrors() {
    return {
      default: 'Something went wrong!'
    }
  }

  public getAuthJson(): any {
    return this.http.get(this.endpoint.auth);
  }

  public setMessageJsonData(data): void {
    this.messageJson = data;
  }

  public getMessageJsonData(): any {
    return this.messageJson;
  }

  public showError(message: string): void {
    const config = new MatSnackBarConfig();
    config.duration = 6000;
    this.snackBar.open(message, null, config);
  }
}
